:root {
  --primary-color: #c3c3c3;
}

body {
  background-color: #fff;
  font-family: 'Exo 2', Lato, 'PT Sans', -apple-system, BlinkMacSystemFont,
    Roboto, Ubuntu, 'Droid Sans', 'Helvetica Neue', Arial, sans-serif;
  font-size: 1rem;
}

:root {
  --primary-color: #ccc;
}

.main {
  border: 0.4rem solid var(--primary-color);
  border-radius: 50%;
  background-color: #ccc;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 0.4rem;
  height: 50vw;
  margin: 4rem auto;
  width: 50vw;
}

.buttonGame {
  cursor: pointer;
  background-size: cover;
  background-repeat: no-repeat;
}

.--blink {
  filter: opacity(0.4);
}

.button__UL {
  background-image: url("data:image/svg+xml;utf8, <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 100 100' id='sector_xY'><path fill='green'  d='M100,100 L0,100 A100,100 1 0,1 100,0z' /></svg>");
}

.button__UR {
  background-image: url("data:image/svg+xml;utf8, <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 100 100' id='sector_XY'><path fill='red'  d='M0,100 L0,0 A100,100 1 0,1 100,100 z' /></svg>");
}

.button__LL {
  background-image: url("data:image/svg+xml;utf8, <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 100 100' id='sector_xy'><path fill='yellow'  d='M100,0 L100,100 A100,100 1 0,1 0,0 z' /></svg>");
}

.button__LR {
  background-image: url("data:image/svg+xml;utf8, <svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 100 100' id='sector_Xy'><path fill='blue'  d='M0,0 L100,0 A100,100 1 0,1 0,100 z' /></svg>");
}

html {
  box-sizing: border-box;
  height: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

div,
p {
  margin: 0;
  padding: 0;
}
